.footer {
  background: #b3b175;
  font-size: 24px;
  color: #dfdd94;
  transition: 0.3s;
  appearance: none;
}

.header {
  background: #674be6;
}

@media (max-width: 920px) {
  .footer {
    background: #63c2da;
  }

  .header {
    background: #7681a5;
  }
}

@media (max-width: 560px) {
  .footer {
    background: #da63b6;
  }
}